      border: 1px solid #333333; }
    .nav__btn::after {
      content: '';
      width: calc(100% - 2px);
      height: 1px;
      border: 1px solid #333333;
      position: absolute;
      top: 0;
      left: 0; }
    .nav__btn::before {
      content: '';
      width: calc(100% - 2px);
      height: 1px;
      border: 1px solid #333333;
      position: absolute;
      bottom: 0;
      left: 0; }
  .nav__list {
